/* ====================== Restaurant Listing ====================== */
#restaurants-list {
    background-color: #f3f3f3;
    list-style: outside none none;
    margin: 0;
    padding: 30px 15px 60px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    li {
        background-color: #fff;
        border: 2px solid #ccc;
        font-family: Arial,sans-serif;
        margin: 15px;
        min-height: 380px;
        padding: 0 30px 25px;
        text-align: left;
        width: 90%;
        display: flex;
        align-items: center;
        flex-direction: column;

        h1 {
            color: #f18200;
            font-family: Arial,sans-serif;
            font-size: 14pt;
            font-weight: 200;
            letter-spacing: 0;
            line-height: 1.3;
            margin: 20px 0 10px;
            text-transform: uppercase;
        }

        a {
            background-color: orange;
            border-bottom: 3px solid #eee;
            color: #fff;
            display: inline-block;
            font-size: 10pt;
            margin: 15px 0 0;
            padding: 8px 30px 10px;
            text-align: center;
            text-decoration: none;
            text-transform: uppercase;
        }
    }

      .restaurant-img {
        background-color: #ccc;
        display: block;
        margin: 0;
        max-width: 100%;
        min-height: 248px;
        min-width: 100%;
      }

      p {
        margin: 0;
        font-size: 11pt;
      }
  }
